* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  padding-top: 60px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  display: block;
}

.search-form {
  width: 100%;
  background-color: #fff;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

button.is-hidden {
  display: none;
}

button {
  letter-spacing: .06em;
  min-width: 200px;
  color: #00f;
  cursor: pointer;
  background-color: #deb887;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-left: 20px;
  padding-top: 5px;
  padding-bottom: 5px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  transition-property: background-color, color, border;
  transition-duration: .5s;
  transition-timing-function: linear;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

button:hover {
  color: #090933;
  background-color: #b3884d;
  border: 1px solid #090933;
  animation: animate125 2s 10ms infinite;
}

.input-search {
  width: 50%;
  height: 40px;
  border-radius: 4px;
  margin-right: 20px;
  padding: 2px 10px;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  padding: 0;
  display: grid;
}

.info {
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-item:not(:last-child) {
  margin-right: 15px;
}

.gallery img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

